//Width
$mobile-width: 320px;
$tablet-width: 768px;
$desktop-width: 1200px;

//Colors
$white: rgb(255, 255, 255);
$black: rgb(0, 0, 0);

$blue: rgb(129, 179, 210);
$blue-hover: rgb(102, 158, 192);
$blue-active: rgb(84, 150, 189);

$brown: rgb(118, 99, 87);

$grey: rgb(242, 242, 242);
$grey-hover: rgb(229, 229, 229);
$grey-active: rgb(213, 213, 213);

$grey-card-alt:rgb(234, 234, 234);

$grey-controls: rgb(51, 51, 51);
$grey-input: rgb(230, 230, 230);
$grey-border-form: rgb(219, 219, 219);
$grey-disabled: rgb(229, 229, 229);

$video-player: rgb(92, 104, 112);
$video-player-panel-bottom: rgb(242, 242, 242);

$grey-decor: rgb(238, 238, 238);
$decor-line: rgb(214, 214, 214);


//Shadows
$shadow-popup: 0 4px 4px rgba(0, 0, 0, 0.25);
$shadow-menu: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);

$shadow-desktop: 0 5px 15px rgba(0, 1, 1, 0.2);

$box-shadow: 0 -1.5px 0 rgba(1, 1, 1, 0.2);
$box-shadow-2: inset 0 1.5px 0 rgba(255, 255, 255, 0.2);
